 .sign-up-bg {
    object-fit: cover;
    width: 100%;
    height: 100vh;
} 

.sign-up-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: -725px;
}


.register-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5px;
    margin: 5px;
}

.sign-up-form-heading {
    color: #fff;
    font-size: 20px;
    letter-spacing: 2px;
}

.sign-up-inpt {
    width: 300px;
    height: 30px;
    border: none;
    border-radius: 10px;
    background-color: #242424;
    text-align: center;
    color: #fff;
    margin: 10px;
    letter-spacing: 2px;
}

.sign-up-btn-1 {
    width: 280px;
    height: 30px;
    border: none;
    border-radius: 10px;
    background-color:#dcdcdc;
    letter-spacing: 1px;
    cursor: pointer;
    transition: 1s;
    margin: 10px 0;
    color: #242424;
}

.sign-up-btn-1:hover {
    background-color: #000;
    color: #fff;
}

.sign-up-btn-2 {
    width: 280px;
    height: 30px;
    border: none;
    border-radius: 10px;
    background-color:#dcdcdc;
    letter-spacing: 1px;
    cursor: pointer;
    transition: 1s;
    margin-bottom: 20px;
}

.sign-up-btn-2:hover {
    background-color: #000;
    color: #fff;
}



/* T A B L E T / D E S K T O P */
@media only screen and (min-width: 768px){
    .sign-up-form {
        margin-top:  -900px;
    }

    .sign-up-form-heading {
        font-size: 25px;
        letter-spacing: 3px;
    }

    .sign-up-inpt {
        width: 400px;
        height: 40px;
        font-size: 18px;
    }

    .sign-up-btn-1 {
        margin-top: 20px;
        width: 350px;
        height: 35px;
        font-size: 18px;
    }

    .sign-up-btn-2 {
        margin-top: 10px;
        width: 300px;
        height: 30px;
        font-size: 16px;
        background-color: #dcdcdc;
    }
}





